import styled from "styled-components";
import { IGrid } from "../types/Interfaces";
import { Div } from "./Overrides";

export const Grid = styled(Div)`
  display: grid;
  grid-gap: ${(props: IGrid) => (props.gap ? props.gap : 0)};
  grid-template-columns: ${(props: IGrid) =>
    `repeat(${props.cols},${props.colsWidth ? props.colsWidth : "1fr"})`};
  @media (max-width: 768px) {
    grid-template-columns: ${(props: IGrid) =>
      `repeat(${props.responsive ? "auto-fit" : props.cols},${
        props.colsWidthResponsive
          ? props.colsWidthResponsive
          : props.colsWidth
          ? props.colsWidth
          : "1fr"
      })`};
  }
`;
