import React, { useState, useEffect, useMemo } from "react";
import Axios from "axios";
import useLocalStorage from "./useLocalStorage";
import { baseURL } from "index";
const UsuarioContext = React.createContext();
export function UsuarioProvider(props) {
  const [usuario, setUsuario] = useState(null);
  const [cargandoUsuario, setCargandoUsuario] = useState(true);
  const [setLogin,getLogin] = useLocalStorage("login")
  useEffect(() => {
    recuperarUsuario();
    return () => {};
  //eslint-disable-next-line
  }, []);
  const recuperarUsuario = async (e) => {
    setCargandoUsuario(true);
    const store = getLogin();
    if (store) {
      setUsuario(store);
    }
    setCargandoUsuario(false);
  };
  const login = async (username, password) => {
    setCargandoUsuario(true);
    try {
      const res = await Axios.post(`${baseURL}/login/`, { username, password });
      const { token, user, idrol, idEmpleado, dni } = res.data;
      setLogin({ token, username: user, idrol, idEmpleado , dni })
      recuperarUsuario();
      setCargandoUsuario(false);
      return [
        "AUTENTICACION CORRECTA",
        "Bienvenido " + user + "!",
        200,
      ];
    } catch (error) {
      setCargandoUsuario(false);
      return [
        "AUTENTICACION INCORRECTA",
        "VERIFIQUE SU USUARIO Y CONTRASEÑA",
        501,
      ];
    }
  };
  const logout = (e) => {
    localStorage.removeItem("login");
    sessionStorage.clear();
    setUsuario(null);
  };
  
  const value = useMemo(() => {
    return {
      usuario,
      cargandoUsuario,
      login,
      logout,
    };
    //eslint-disable-next-line
  }, [usuario, cargandoUsuario]);
  return <UsuarioContext.Provider value={value} {...props} />;
}
export function useUsuario() {
  const context = React.useContext(UsuarioContext);
  if (!context) {
    throw new Error("No se encontro Usuario en el contexto");
  }
  return context;
}