import { lazy } from "react";

const Home = lazy(() => import("pages/Home"));
const Login = lazy(() => import("pages/Login"));
const Hospitalizacion = lazy(() => import("pages/Hospitalizacion"));
const General = lazy(() => import("pages/General"));
const Admision = lazy(() => import("pages/Admision"));
const Adminision = lazy(() => import("pages/Adminision"));
const Atencion = lazy(() => import("pages/Atencion"));
const CitasWeb = lazy(() => import("pages/CitasWeb"));
const COVID = lazy(() => import("pages/COVID"));
const COVID19 = lazy(() => import("pages/COVID-19"));
const Epidemiologia = lazy(() => import("pages/Epidemiologia"));
const Files = lazy(() => import("pages/Files"));
const HospitalizacionFarmacia = lazy(() => import("pages/HospitalizacionFarmacia"));
const HospitalizacionHemodialisis = lazy(() => import("pages/HospitalizacionHemodialisis"));
const Imagen = lazy(() => import("pages/Imagen"));
const Laboratorio = lazy(() => import("pages/Laboratorio"));
const Medicos = lazy(() => import("pages/Medicos"));
const Modulo = lazy(() => import("pages/Modulo"));
const Reporte = lazy(() => import("pages/Reporte"));
const RRHH = lazy(() => import("pages/RRHH"));

export const MapRoutes = [
    { path: "/", component: Home },
    { path: "/Login", component: Login },
    { path: "/Hospitalizacion", component: Hospitalizacion },
    { path: "/General", component: General },
    { path: "/Admision", component: Admision },
    { path: "/Adminision", component: Adminision },
    { path: "/AtencionMedica", component: Atencion },
    { path: "/CitasWeb", component: CitasWeb },
    { path: "/COVID-19", component: COVID },
    { path: "/COVID-19DEFUNCIONES", component: COVID19 },
    { path: "/Epidemiologia", component: Epidemiologia },
    { path: "/Files", component: Files },
    { path: "/Farmacia", component: HospitalizacionFarmacia },
    { path: "/Hemodialisis", component: HospitalizacionHemodialisis },
    { path: "/Imagenes", component: Imagen },
    { path: "/Laboratorio", component: Laboratorio },
    { path: "/Medicos", component: Medicos },
    { path: "/ModuloAdministrarCitasWeb", component: Modulo },
    { path: "/Reportes", component: Reporte },
    { path: "/RRHH", component: RRHH },
]