import { Suspense } from "react";
import { Route, Switch } from "react-router-dom";
import { MapRoutes } from "routing/RouteMap";

const Router = () => {
  return (
    <Switch>
      <Suspense fallback={<p>Cargando...</p>}>
        {MapRoutes.map((el, idx) => (
          <Route exact path={el.path} key={idx} component={el.component} />
        ))}
      </Suspense>
    </Switch>
  );
};

export default Router;
