import { Menu } from "antd";
import { Div } from "mylibs/util-sc";
import { useHistory } from "react-router-dom";
import { MenuStyled } from "./styled";

const menus = [
  "General",
  "Atención medica",
  "Hospitalizacion",
  "Reportes",
  "Farmacia",
  "Hemodialisis",
  "Laboratorio",
  "COVID-19",
  "Imagenes",
  "Citas Web",
  "Modulo Administrar Citas Web",
  "COVID-19 DEFUNCIONES",
  "Admision",
  "Adminision",
  "RRHH",
  "Medicos",
  "Epidemiología",
];

const LateralMenu = () => {
  const { push } = useHistory()

  const handleRoute = (name : string) => {
    push(`/${name.replaceAll(" ","").normalize("NFD").replace(/[\u0300-\u036f]/g, "")}`)
  }

  return (
    <Div width="20%">
      <MenuStyled mode="inline">
        {menus.map((item: string, key: number) => (
          <Menu.Item key={key} onClick={()=>handleRoute(item)}>{item}</Menu.Item>
        ))}
      </MenuStyled>
    </Div>
  );
};

export default LateralMenu;
