import { Row } from "mylibs/util-sc";
import { Fragment } from "react";

interface ItemProps {
  icon?: string;
  label: string;
  disabled?: boolean;
  children?: any;
  extra?: any;
}

const Item = ({
  icon,
  label,
  disabled = false,
  children,
  extra = false,
}: ItemProps) => {
  const Label = (
    <Fragment>
      <i className={`fa fa-${icon}`}></i> <strong>{label} :</strong>
    </Fragment>
  );
  return !disabled ? (
    <p>
      {extra ? (
        <Row justifyContent="space-between">
          <p>{Label}</p>
          {extra}
        </Row>
      ) : (
        Label
      )}
      {children}
    </p>
  ) : (
    <Fragment />
  );
};

export default Item;
